input[type=search].tw-dialog-search {
  position: relative;
  min-width: 480px;
  width: 100%;
  appearance: none;
  padding: 9px 15px;
  background: #fafafa;
  border: 1px solid #eaeaea;
}

.tw-dialog-results {
  position: relative;
  display: flex;
  margin: 24px -24px 0;
  max-width: 528px;
  flex-flow: column;
}
.tw-dialog-results.is-loading {
  align-items: center;
}
.tw-dialog-results .components-notice {
  margin: 0 24px;
  flex: 1 1 auto;
}
.tw-dialog-result {
  position: relative;
  display: flex;
  padding: 15px 24px;
  width: inherit;
  flex-flow: column;
  justify-content: flex-start;
  border-bottom: 1px solid #eaeaea;
  color: unset;
  cursor: pointer;
  overflow: hidden;
}
.tw-dialog-result:first-child {
  border-top: 1px solid #eaeaea;
}
.tw-dialog-result:last-child {
  margin-bottom: -15px;
  border-bottom: 0;
}
.tw-dialog-result:hover {
  background: #fafafa;
}
.tw-dialog-result span {
  margin-top: 3px;
  width: 100%;
  opacity: 0.75;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

