.tw-sortable-list {
  position: relative;
  display: flex;
  margin: -5px -16px 0;
  width: calc(100% + 32px);
  flex-flow: column;
}
.tw-sortable-list-item {
  position: relative;
  display: flex;
  padding: 15px 16px;
  align-items: flex-start;
  background: white;
  border-top: 1px solid #f0f0f0;
  border-bottom: 1px solid #f0f0f0;
}
.tw-sortable-list-item-body {
  position: relative;
  display: flex;
  flex: 1 1 auto;
  flex-flow: column;
}
.tw-sortable-list-item-body span {
  opacity: 0.75;
}
.tw-sortable-list-item-body strong + span {
  margin-top: 3px;
}
.tw-sortable-list-item-delete, .tw-sortable-list-item-handle {
  position: relative;
  display: flex;
  padding: 3px;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0.75;
}
.tw-sortable-list-item-delete svg, .tw-sortable-list-item-handle svg {
  height: 12px;
}
.tw-sortable-list-item-delete:hover, .tw-sortable-list-item-handle:hover {
  opacity: 1;
}
.tw-sortable-list-item-delete {
  margin-left: 9px;
  margin-right: 6px;
  color: crimson !important;
}
.tw-sortable-list-item-handle {
  margin-left: -3px;
  margin-right: 9px;
  color: #404040 !important;
  cursor: move;
}
.tw-sortable-list-item + .tw-sortable-list-item {
  margin-top: -1px;
}